import React, { useState } from 'react';
import styled from '@emotion/styled';

const Step = styled.div`
  display: flex;
  flex-direction: column;
  width: 800px;
  margin: 0 auto;
`;
const StepHeading = styled.h2`
  font-size: 24px;
  margin: 48px 0 0;
`;
const StepTextArea = styled.textarea`
  font-family: serif;
  font-size: 20px;
  width: 100%;
  padding: 12px 10px;
  margin: 32px 0;
  resize: vertical;
`;

const NextButton = styled.button`
  font-size: 20px;
  margin-left: auto;
`;

function App() {
  const [currentStep, setCurrentStep] = useState('subjectMatter');
  const [subjectMatter, setSubjectMatter] = useState('');
  const [rule, setRule] = useState('');

  switch (currentStep) {
    case 'subjectMatter':
      return (
        <Step>
          <StepHeading>What is the subject matter you’re testing?</StepHeading>
          <StepTextArea
            value={subjectMatter}
            onChange={({ target: { value } }) => {
              setSubjectMatter(value);
            }}
            placeholder="Example: The U.C.C. Statute of Frauds"
            rows={1}
          />
          <NextButton
            disabled={!subjectMatter}
            onClick={() => {
              setCurrentStep('rule');
            }}
          >
            Next
          </NextButton>
        </Step>
      );
    case 'rule':
      return (
        <Step>
          <StepHeading>State the rule you’re testing.</StepHeading>
          <StepTextArea
            value={rule}
            onChange={({ target: { value } }) => {
              setRule(value);
            }}
            placeholder="Example: U.C.C. § 2-201(1). Except as otherwise provided in this section a contract for the sale of goods for the price of $500 or more is not enforceable by way of action or defense unless there is some writing sufficient to indicate that a contract for sale has been made between the parties and signed by the party against whom enforcement is sought or by his authorized agent or broker. A writing is not insufficient because it omits or incorrectly states a term agreed upon but the contract is not enforceable under this paragraph beyond the quantity of goods shown in such writing."
            rows={10}
          />
          <NextButton
            disabled={!rule}
            onClick={() => {
              setCurrentStep('todo');
            }}
          >
            Next
          </NextButton>
        </Step>
      );
    default:
      return (
        <Step>
          <p style={{ fontSize: 24 }}>
            Additional steps have not yet been implemented.
          </p>
        </Step>
      );
  }
}

export default App;
